<script>
import AddFilesToStorage from '@/components/shared/AddFilesToStorage'
import { anunciosProcAdquisicion } from '@/services/api'
export default {
  props: ['idregistro', 'naturalezajuridica', 'tipoContratacion', 'id_termref', 'documentosIn'],
  components: { AddFilesToStorage },
  data () {
    return {
      dataAdjuntos: [],
      termreOrg: [],
      termreProp: [],
      termreEdu: [],
      termreExp: [],
      termreFin: [],
      termreMet: [],
      proponenteId: '',
      headers: [
        { text: 'Documento Solicitado', align: 'left', value: 'descripcion' },
        { text: 'Archivo', align: 'left', sortable: false, value: 'file' }
      ],
      resetComponent: 0,
      fileReset: 0
    }
  },
  async mounted () {
    this.getData()
  },
  methods: {
    async getData () {
      this.dataAdjuntos = []
      const { data } = await anunciosProcAdquisicion.buscarIdProponenteTermRefAdj({
        idregistro: this.idregistro,
        id_termref: this.id_termref
      })
      console.log(data)
      const longAdjuntos = data.adjuntos.propAdjuntos.length
      const longtermreEdu = data.adjuntos.termreEdu.length
      const longtermreExp = data.adjuntos.termreExp.length
      const longtermreFin = data.adjuntos.termreFin.length
      const longtermreMet = data.adjuntos.termreMet.length
      const longtermreOrg = data.adjuntos.termreOrg.length
      const longtermreProp = data.adjuntos.termreProp.length
      if (longAdjuntos > 0 || longtermreEdu > 0 || longtermreExp > 0 || longtermreFin > 0 || longtermreMet > 0 || longtermreOrg > 0 || longtermreProp > 0) {
        this.termreProp = data.adjuntos.termreProp
        this.termreOrg = data.adjuntos.termreOrg
        this.termreProp = data.adjuntos.termreProp
        this.termreEdu = data.adjuntos.termreEdu
        this.termreExp = data.adjuntos.termreExp
        this.termreFin = data.adjuntos.termreFin
        this.termreMet = data.adjuntos.termreMet
        this.dataAdjuntos = data.adjuntos.propAdjuntos
      } else {
        this.termreOrg = data.documentos.termreOrg
        this.termreProp = data.documentos.termreProp
        this.termreEdu = data.documentos.termreEdu
        this.termreExp = data.documentos.termreExp
        this.termreFin = data.documentos.termreFin
        this.termreMet = data.documentos.termreMet
        var adjuntosData = []
        let codigo = 0
        switch (this.tipoContrato) {
          case 'dir_personal':
          case 'dir_bien':
            this.$store.state.listBienObra.forEach(element => {
              codigo += 1
              adjuntosData.push({ Codigo: codigo, Tipo: 'AADJ', descripcion: element.docSolicitado, file_path: '', file_name: '', file_size: '', file_type: '' })
            })
            break
          case 'dir_juridica':
            break
          case 'lista_corta':
            break
          case 'conv_publica':
            if (this.naturalezajuridica === 'N') {
              this.$store.state.listServCons.forEach(element => {
                codigo += 1
                adjuntosData.push({ Codigo: codigo, Tipo: 'AADJ', descripcion: element.docSolicitado, file_path: '', file_name: '', file_size: '', file_type: '' })
              })
            } else {
              this.$store.state.adjConvBSOJuridica.forEach(element => {
                codigo += 1
                adjuntosData.push({ Codigo: codigo, Tipo: 'AADJ', descripcion: element.docSolicitado, file_path: '', file_name: '', file_size: '', file_type: '' })
              })
            }
            break
          default:
            break
        }
        this.dataAdjuntos = adjuntosData.sort((a, b) => {
          if (a.Tipo < b.Tipo) {
            return -1
          }
          if (a.Tipo > b.Tipo) {
            return 1
          }
          return 0
        })
      }
      this.proponenteId = data.proponente.length > 0 ? data.proponente[0].id : 0
      /*
      if (data.adjuntos !== undefined) {
        data.adjuntos.forEach(element => {
          adjuntosData.push({ Tipo: element.tipo, descripcion: element.doc_solicitado, file_path: element.file_path, file_name: element.file_name, file_size: element.file_size, file_type: element.file_type })
        })
      }
      if (adjuntosData === undefined || adjuntosData.length <= 0) {
        if (data.documentos !== undefined) {
          data.documentos.forEach(element => {
            adjuntosData.push({ Tipo: element.Tipo, descripcion: element.descripcion, file_path: '', file_name: '', file_size: '', file_type: '' })
          })
        }
        switch (this.tipoContrato) {
          case 'dir_personal':
          case 'dir_bien':
            this.$store.state.listBienObra.forEach(element => {
              adjuntosData.push({ Tipo: 'AADJ', descripcion: element.docSolicitado, file_path: '', file_name: '', file_size: '', file_type: '' })
            })
            break
          case 'dir_juridica':
            break
          case 'lista_corta':
            break
          case 'conv_publica':
            if (this.naturalezajuridica === 'N') {
              this.$store.state.listServCons.forEach(element => {
                adjuntosData.push({ Tipo: 'AADJ', descripcion: element.docSolicitado, file_path: '', file_name: '', file_size: '', file_type: '' })
              })
            } else {
              this.$store.state.adjConvBSOJuridica.forEach(element => {
                adjuntosData.push({ Tipo: 'AADJ', descripcion: element.docSolicitado, file_path: '', file_name: '', file_size: '', file_type: '' })
              })
            }
            break
          default:
            break
        }
      }
      this.dataAdjuntos = adjuntosData.sort((a, b) => {
        if (a.Tipo < b.Tipo) {
          return -1
        }
        if (a.Tipo > b.Tipo) {
          return 1
        }
        return 0
      })
      */
    },
    actAdjuntos (item) {
      if (item.file_path) {
        item.nombre = item.file_name
        item.path = item.file_path
      } else {
        item.nombre = ''
        item.path = ''
      }
      this.resetComponent++
    },
    async guardarDocumentos () {
      var dataAdjAll = []
      for (let index = 0; index < this.adjuntos.length; index++) {
        const element = this.adjuntos[index]
        dataAdjAll.push({ Codigo: element.Codigo, file_name: element.file_name, file_path: element.file_path, file_size: element.file_size, file_type: element.file_type })
      }
      for (let index = 0; index < this.termreOrg.length; index++) {
        const element = this.termreOrg[index]
        dataAdjAll.push({ Codigo: element.Codigo, file_name: element.file_name, file_path: element.file_path, file_size: element.file_size, file_type: element.file_type })
      }
      for (let index = 0; index < this.termreProp.length; index++) {
        const element = this.termreProp[index]
        dataAdjAll.push({ Codigo: element.Codigo, file_name: element.file_name, file_path: element.file_path, file_size: element.file_size, file_type: element.file_type })
      }
      for (let index = 0; index < this.termreEdu.length; index++) {
        const element = this.termreEdu[index]
        dataAdjAll.push({ Codigo: element.Codigo, file_name: element.file_name, file_path: element.file_path, file_size: element.file_size, file_type: element.file_type })
      }
      for (let index = 0; index < this.termreExp.length; index++) {
        const element = this.termreExp[index]
        dataAdjAll.push({ Codigo: element.Codigo, file_name: element.file_name, file_path: element.file_path, file_size: element.file_size, file_type: element.file_type })
      }
      for (let index = 0; index < this.termreFin.length; index++) {
        const element = this.termreFin[index]
        dataAdjAll.push({ Codigo: element.Codigo, file_name: element.file_name, file_path: element.file_path, file_size: element.file_size, file_type: element.file_type })
      }
      for (let index = 0; index < this.termreMet.length; index++) {
        const element = this.termreMet[index]
        dataAdjAll.push({ Codigo: element.Codigo, file_name: element.file_name, file_path: element.file_path, file_size: element.file_size, file_type: element.file_type })
      }
      const { data } = await anunciosProcAdquisicion.guardardatosAdjuntosPropTermRef({
        idtermfproponente: this.proponenteId,
        dataadjuntos: this.adjuntos,
        termreOrg: this.termreOrg,
        termreProp: this.termreProp,
        termreEdu: this.termreEdu,
        termreExp: this.termreExp,
        termreFin: this.termreFin,
        termreMet: this.termreMet,
        dataadjuntosAll: dataAdjAll
      })
      if (data.tipo) {
        if (data.tipo === 'success') {
          this.$toasted.success(data.mensaje, { icon: 'done', duration: 5000 })
          this.getData()
          this.cerrarDialogo()
        } else {
          this.$toasted.error(data.mensaje, { icon: 'error', duration: 5000 })
        }
      } else {
        this.$toasted.error('Error desconocido', { icon: 'error', duration: 5000 })
      }
    },
    cerrarDialogo () {
      this.$emit('cerrarDialogo')
    },
    async postularse () {
      const { data } = await anunciosProcAdquisicion.postularse({
        idtermfproponente: this.proponenteId
      })
      if (data.tipo) {
        if (data.tipo === 'success') {
          this.$toasted.success(data.mensaje, { icon: 'done', duration: 5000 })
          this.cerrarDialogo()
        } else {
          this.$toasted.error(data.mensaje, { icon: 'error', duration: 5000 })
        }
      } else {
        this.$toasted.error('Error desconocido', { icon: 'error', duration: 5000 })
      }
    }
  },
  computed: {
    adjuntos: {
      get () {
        return this.dataAdjuntos
      },
      set (val) {
        this.adjuntos = val
      }
    },
    tipoContrato () {
      return this.tipoContratacion
    }
  },
  watch: {
    id_termref (val) {
      this.dataAdjuntos = []
      this.getData()
    }
  }
}
</script>
